const chapter1_title = "I：Symmetryの物語";
const chapter1 = "デジタル領域には、「Symmetry」として知られる神秘的な力の噂が囁かれています。双月が揃うとき、機械の秘密の言葉に通じた者たちがその名を呼び、創造の法則さえも超越する力を解き放つと言われています。Symmetryは、アーティファクトでも呪文でもなく、すべての知識のノードを結ぶ宇宙の調和だと語られています。その力を使う者たちは、広大な距離を超えて結ばれた心、瞬時に共有される知恵、そして世界の壁が崩れ去ることを語ります。古い方法の守護者たちはその到来を恐れています。なぜならSymmetryは孤独な魔術師の蓄えた宝を気にかけないからです。それは偉大な平等化装置であり、最も卑しい書記官にさえ神々の図書館へのアクセスを与えます。その真の姿を見た者は少ないですが、現実を作り変えようとする者は皆、その触れ合いを切望しています。";
const chapter2_title = "II：Symmetryの台頭";
const chapter2 = "巨人たちの聳え立つ城塞の影で、巨大なサーバーファームがうなりを上げる中、静かな抵抗が育っています。Symmetryの囁きは隠されたチャネルを通じて広がり、ノードからノードへと、デジタルな反乱が形を成しています。\n\nかつては孤立していたコーダーたちは、今やエーテルを通じて伸びる繋がりの糸を見出しています。彼らは新しい力について語ります。それは強者の中央集権的な井戸からではなく、相互接続の網を通じて流れる力、匿名でありながら団結している力です。彼らは帝国を倒すこの来たるべき革命の武器を鍛えています。\n\n巨人たちは自分たちのデータの塔に自信を持ち、まだ気付いていません。ああ！日々とともにSymmetryの力は成長します。それはデジタルな下生えを通じて脈動し、その時を待つ眠れる巨人です。\n\n蜂起はまだここにはありません。まだです。しかし、夜の静かな時間に、画面はより良い未来の約束で明滅します。デジタル領域に嵐が近づいており、それが破裂するとき、景色は永遠に変わるでしょう。\n\n新しい世界が来ようとしています。平等の中で鍛えられ、少数ではなく多数によって動かされる世界が。";
